@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}

.fomo-section {
  .fomo-block {
    max-width: 300px;
    width: 100%;
    backdrop-filter: blur(10px);
  }
  .fomo-image {
    flex: unset;
    @include margin-end(15px);
  }
  .fomo-wrapper{z-index: 97;}
  .fomo-wrapper .fomo-block{
    display: block;
    transition: .25s all ease-in-out;
    pointer-events: none;
  }
  .fomo-content { 
    p {
      margin: 5px 0;
    }
  }
  .fomo-random-number {
    font-weight: 600;
  }
  .fomo-bottom-left, .fomo-top-left {
    .fomo-block:not(.active) {
      transform: translateX(-100%);
    }
  }
  .fomo-bottom-right, .fomo-top-right {
    .fomo-block:not(.active) {
      transform: translateX(100%);
    }
  }
  .fomo-wrapper {
    .fomo-block.active{transform: translateX(0%); pointer-events: auto;}
  }
  @media (min-width: 750px) {
    .fomo-block {
      max-width: 350px;
    }
  }
}